const DATA = callData;
export function getData(key, value) {
    let KEY = '';
    if(key === 'area'){
        // 
        KEY = 'rdnmadr';
    }
    const VAL = value;
    let result = [];

    if(KEY === 'rdnmadr'){
        DATA.map(value =>{
            //   
            if(value[KEY] === ''){
                //  
                if(value.lnmadr.indexOf(VAL) > -1){
                    result.push(value);
                }
            }else{ 
                if(value[KEY].indexOf(VAL) > -1){
                    result.push(value);
                }
            }
        })
    }

    return result;
}

